import Example from './pages/Example/Example'
import Footer from './global/Footer/Footer'


function App() {
  return (
    <div className="container">
      <Header />
      <HashRouter>
        <Route path='/' element={<Home />} />
        <Route path='/example' exact element={<Example />} />
      </HashRouter>
      <Footer />
    </div>
  );
}
export default App;